import React, { useState } from 'react';
import logo from '../assets/logo.png';
import { useNavigate } from 'react-router-dom';

const Navbar = () => {
  
  const [activeNav, setActiveNav] = useState('');

  const handleLogoClick = () => {
    setActiveNav(''); // Optionally reset activeNav when logo is clicked
  };

  const handleNavClick = (section) => {
    setActiveNav(section);
  };

  return (
    <nav className="navbar sticky-top navbar-expand-lg bg-white">
      <div className="container-fluid">
        <a className="navbar-brand" href="/" onClick={handleLogoClick}>
          <img className="logo" src={logo} alt="Alaa Ahmed logo" />
        </a>
        <button
          className="navbar-toggler"
          type="button"
          data-bs-toggle="collapse"
          data-bs-target="#navbarSupportedContent"
          aria-controls="navbarSupportedContent"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span className="navbar-toggler-icon"></span>
        </button>
        <div className="collapse navbar-collapse" id="navbarSupportedContent">
          <ul className="navbar-nav me-auto mb-2 mb-lg-0">
            <li className="nav-item">
              <a
                className={`nav-link ${activeNav === 'aboutMe' ? 'active' : ''}`}
                href="#aboutMe"
                onClick={() => handleNavClick('aboutMe')}
              >
                About Me
              </a>
            </li>
            <li className="nav-item">
              <a
                className={`nav-link ${activeNav === 'edu' ? 'active' : ''}`}
                href="#edu"
                onClick={() => handleNavClick('edu')}
              >
                Education
              </a>
            </li>
            <li className="nav-item">
              <a
                className={`nav-link ${activeNav === 'exp' ? 'active' : ''}`}
                href="#exp"
                onClick={() => handleNavClick('exp')}
              >
                Experience
              </a>
            </li>
            <li className="nav-item">
              <a
                className={`nav-link ${activeNav === 'intren' ? 'active' : ''}`}
                href="#intren"
                onClick={() => handleNavClick('intren')}
              >
                Internships
              </a>
            </li>
            <li className="nav-item">
              <a
                className={`nav-link ${activeNav === 'Act' ? 'active' : ''}`}
                href="#Act"
                onClick={() => handleNavClick('Act')}
              >
                Activities
              </a>
            </li>
            <li className="nav-item">
              <a
                className={`nav-link ${activeNav === 'pro' ? 'active' : ''}`}
                href="#pro"
                onClick={() => handleNavClick('pro')}
              >
                Projects
              </a>
            </li>
            <li className="nav-item">
              <a
                className={`nav-link ${activeNav === 'fun' ? 'active' : ''}`}
                href="#fun"
                onClick={() => handleNavClick('fun')}
              >
                Fun Facts
              </a>
            </li>
            <li className="nav-item">
              <a
                className={`nav-link ${activeNav === 'contact' ? 'active' : ''}`}
                href="#contact"
                onClick={() => handleNavClick('contact')}
              >
                Contact Me
              </a>
            </li>
          </ul>
        </div>
      </div>
    </nav>
  );
};

export default Navbar;
